.van-slider {
  position: relative;
  height: 0.08533rem;
  height: var(--slider-bar-height, 0.08533rem);
  border-radius: 21.312rem;
  border-radius: var(--border-radius-max, 21.312rem);
  background-color: #ebedf0;
  background-color: var(--slider-inactive-background-color, #ebedf0);
}
.van-slider::before {
  position: absolute;
  right: 0;
  left: 0;
  content: '';
  top: -0.34133rem;
  top: -var(--padding-xs, 0.34133rem);
  bottom: -0.34133rem;
  bottom: -var(--padding-xs, 0.34133rem);
}
.van-slider__bar {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #1989fa;
  background-color: var(--slider-active-background-color, #1989fa);
  border-radius: inherit;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transition: all var(--animation-duration-fast, 0.2s);
  transition: all var(--animation-duration-fast, 0.2s);
}
.van-slider__button {
  width: 1.024rem;
  width: var(--slider-button-width, 1.024rem);
  height: 1.024rem;
  height: var(--slider-button-height, 1.024rem);
  border-radius: 50%;
  border-radius: var(--slider-button-border-radius, 50%);
  box-shadow: 0 0.04267rem 0.08533rem rgba(0, 0, 0, 0.5);
  box-shadow: var(--slider-button-box-shadow, 0 0.04267rem 0.08533rem rgba(0, 0, 0, 0.5));
  background-color: #fff;
  background-color: var(--slider-button-background-color, #fff);
}
.van-slider__button-wrapper,
.van-slider__button-wrapper-right {
  position: absolute;
  top: 50%;
  right: 0;
  -webkit-transform: translate3d(50%, -50%, 0);
          transform: translate3d(50%, -50%, 0);
}
.van-slider__button-wrapper-left {
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
}
.van-slider--disabled {
  opacity: 0.5;
  opacity: var(--slider-disabled-opacity, 0.5);
}
.van-slider--vertical {
  display: inline-block;
  width: 0.08533rem;
  width: var(--slider-bar-height, 0.08533rem);
  height: 100%;
}
.van-slider--vertical .van-slider__button-wrapper,
.van-slider--vertical .van-slider__button-wrapper-right {
  top: auto;
  right: 50%;
  bottom: 0;
  -webkit-transform: translate3d(50%, 50%, 0);
          transform: translate3d(50%, 50%, 0);
}
.van-slider--vertical .van-slider__button-wrapper-left {
  top: 0;
  right: 50%;
  left: auto;
  -webkit-transform: translate3d(50%, -50%, 0);
          transform: translate3d(50%, -50%, 0);
}
.van-slider--vertical::before {
  top: 0;
  right: -0.34133rem;
  right: -var(--padding-xs, 0.34133rem);
  bottom: 0;
  left: -0.34133rem;
  left: -var(--padding-xs, 0.34133rem);
}
